import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {
  fillterStatus = 'XEM_TAT_CA';
  name = '';
  isShowForm = false;
  isShow = true;
  newEn = '';
  newVn = '';
  arraysubject =
   [
      { id: 1 , en: 'action', vn: 'hành động', memorized: true},
      { id: 2 , en: 'actor', vn: 'diễn viên', memorized: false},
      { id: 3 , en: 'active', vn: 'hoạt động', memorized: true},
      { id: 4 , en: 'bath', vn: 'tắm', memorized: false}
   ];

      constructor() { }

  ngOnInit() {
  }
  showEvent(event) {
      this.name = event.target.value;
  }
  addWord() {
    this.isShowForm = false;
    this.arraysubject.unshift({
      id: this.arraysubject.length + 1,
      en: this.newEn,
      vn: this.newVn,
      memorized: false
    });
  }
  removeWord(id: number) {
    const index = this.arraysubject.findIndex(e => e.id === id);
    this.arraysubject.splice(index, 1);
  }
  getShowStatus(memorized: boolean) {
    const dkXemtatca = this.fillterStatus === 'XEM_TAT_CA';
    const dkXemdanho = this.fillterStatus === 'XEM_DA_NHO' && memorized;
    const dkXemchuanho = this.fillterStatus === 'XEM_CHUA_NHO' && !memorized;
    return  dkXemtatca || dkXemchuanho || dkXemdanho;

  }
}
